___CSS_LOADER_EXPORT___.push([module.id, `body, html {
    height: 100%;
    width: 100%;
    margin: 0;
    padding: 0;
    background: linear-gradient(135deg, #DCCBA4, #EDE0C8, #DCCBA4); /* Gradiente magnolia con sfumature legno */
    overflow-x: hidden;
}

body {
    transform: translate3d(0,0,0);
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    text-align: center;
}

h1 {
    color:rgb(0, 80, 53);
    font-size: 2rem;
}

h2 {
    color:rgb(11, 0, 80);
    cursor: help;
    font-size: 1.5rem;
}

.border {
    padding: 1rem;
}

main {
    height: auto;
    background: radial-gradient(ellipse 95% 120%, aliceblue, #f5fede, #ffe668);
    margin: 0 auto; /* Centra orizzontalmente */
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid #ccc;
}

p {
    margin: 1px 2px;
    font-size: 1rem;
}

#searchBar, searchButton, #languageSelector {
    margin: 1rem 0;
    padding: 0.5rem;
    border-radius: 5px;
    border: 1px solid #ccc;
    box-sizing: border-box;
    font-size: 1rem;
    max-width: 100vw;;
}

hr {
    width: 100%;
    margin: 1rem auto;
    box-sizing: border-box
}

#results {
    margin-top: 1rem;
    margin-bottom: 1rem;
}

#languageSelector {
    margin: 1rem 0;
    background-color: #fff;
    cursor: pointer;
}

/* Responsive Design */

/* Desktop Layout */
@media (min-width: 1200px) {
    h1 {
        font-size: 2.5rem;
    }
    h2 {
        font-size: 2rem;
    }
    main {
        width: 70%;
    }
    #languageSelector {
        font-size: 1.3rem;
    }
    p {
        font-size: 1.4rem;
    }
    #searchBar, #searchButton {
        font-size: 1.4rem;
    }
}

/* Tablet Layout */
@media (min-width: 768px) and (max-width: 1199px) {
    h1 {
        font-size: 2rem;
    }
    h2 {
        font-size: 1.5rem;
    }
    main {
        width: 80%;
    }
    #languageSelector {
        font-size: 1.1rem;
    }
    p {
        font-size: 1.2rem;
    }
    #searchBar, #searchButton {
        font-size: 1.rem;
    }
}`, "",{"version":3,"sources":["webpack://./css/style.css"],"names":[],"mappings":"AAAA;IACI,YAAY;IACZ,WAAW;IACX,SAAS;IACT,UAAU;IACV,8DAA8D,EAAE,2CAA2C;IAC3G,kBAAkB;AACtB;;AAEA;IACI,6BAA6B;IAC7B,iBAAiB;IACjB,aAAa;IACb,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA;IACI,oBAAoB;IACpB,eAAe;AACnB;;AAEA;IACI,oBAAoB;IACpB,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,0EAA0E;IAC1E,cAAc,EAAE,2BAA2B;IAC3C,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,eAAe;IACf,eAAe;AACnB;;AAEA;IACI,cAAc;IACd,eAAe;IACf,kBAAkB;IAClB,sBAAsB;IACtB,sBAAsB;IACtB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,WAAW;IACX,iBAAiB;IACjB;AACJ;;AAEA;IACI,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,sBAAsB;IACtB,eAAe;AACnB;;AAEA,sBAAsB;;AAEtB,mBAAmB;AACnB;IACI;QACI,iBAAiB;IACrB;IACA;QACI,eAAe;IACnB;IACA;QACI,UAAU;IACd;IACA;QACI,iBAAiB;IACrB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,iBAAiB;IACrB;AACJ;;AAEA,kBAAkB;AAClB;IACI;QACI,eAAe;IACnB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,UAAU;IACd;IACA;QACI,iBAAiB;IACrB;IACA;QACI,iBAAiB;IACrB;IACA;QACI,gBAAgB;IACpB;AACJ","sourcesContent":["body, html {\n    height: 100%;\n    width: 100%;\n    margin: 0;\n    padding: 0;\n    background: linear-gradient(135deg, #DCCBA4, #EDE0C8, #DCCBA4); /* Gradiente magnolia con sfumature legno */\n    overflow-x: hidden;\n}\n\nbody {\n    transform: translate3d(0,0,0);\n    min-height: 100vh;\n    display: flex;\n    flex-direction: column;\n    text-align: center;\n}\n\nh1 {\n    color:rgb(0, 80, 53);\n    font-size: 2rem;\n}\n\nh2 {\n    color:rgb(11, 0, 80);\n    cursor: help;\n    font-size: 1.5rem;\n}\n\n.border {\n    padding: 1rem;\n}\n\nmain {\n    height: auto;\n    background: radial-gradient(ellipse 95% 120%, aliceblue, #f5fede, #ffe668);\n    margin: 0 auto; /* Centra orizzontalmente */\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    border: 1px solid #ccc;\n}\n\np {\n    margin: 1px 2px;\n    font-size: 1rem;\n}\n\n#searchBar, searchButton, #languageSelector {\n    margin: 1rem 0;\n    padding